<template>
  <header>
    <h2>CBS Printing</h2>
  </header>
  <button @click="toggleAdminMode">Toggle Admin Mode</button>
  <ul>
    <li v-for="(item, index) in cbsMenuSubitems" :key="index">
      <print-link
        :link-index="index"
        :link="item.link"
        :link-desc="item.linkDesc"
        :link-target="item.target"
        :admin-mode="
          adminMode &&
          item.link !==
            'https://drive.google.com/drive/folders/1oPPRU47CBxZvXY-_MjEFvzCTDmRE2vHY?usp=sharing'
        "
        @save-item="saveItem"
        @delete-item="deleteItem"
      ></print-link>
    </li>
  </ul>
  <button @click="addNewLink">Add New Link</button>
  <form
    v-if="addMode"
    @submit.prevent="addItem(addedLink, addedLinkDesc, addedLinkTarget)"
  >
    <div>
      <label>Link:</label
      ><input
        @change="addedLinkDesc = $event.target.value"
        type="text"
        v-model="addedLink"
      /><br />
      <label>Link Description:</label
      ><input id="linkDescInput" type="text" v-model="addedLinkDesc" /><br />
      <label>Target:</label
      ><input type="text" v-model="addedLinkTarget" /><br />
      <label></label><button type="submit">Save</button>
      <button @click="addMode = false">Cancel</button>
    </div>
  </form>
  <!-- <button @click="postLinks()">Post Links</button>
  <button @click="putLinks()">Put Links</button>
  <button @click="getLinks()">Get Links</button>
  <button @click="deleteLinks">Delete Links</button>
  <button @click="showLinksData()">Show Links Data</button>
  <button @click="saveAll()">Save All</button> -->
</template>

