    addItem(link, linkDesc, linkTarget) {
      console.log('addItem called for', linkDesc);
      this.cbsMenuSubitems.push({
        link: link,
        linkDesc: linkDesc,
        target: linkTarget,
      });
      const index = this.cbsMenuSubitems.length - 1;
      this.putLink(index);
      this.addMode = false;
    },
    addNewLink() {
      this.addMode = true;
      this.addedLink = '';
      this.addedLinkDesc = '';
      this.addedLinkTarget = '_blank';
    },
    deleteItem(index) {
      console.log('deleteItem called for', index);
      this.cbsMenuSubitems.splice(index, 1);
      console.log(this.cbsMenuSubitems);
      console.log('need to l to database after delete');
      this.deleteLink(index);
    },
