      console.log('saveItem called for', index, link, linkDesc, linkTarget);
      this.cbsMenuSubitems[index].link = link;
      this.cbsMenuSubitems[index].linkDesc = linkDesc;
      this.cbsMenuSubitems[index].target = linkTarget;
      // save this item to database
      this.putLink(index);
