      console.log('postLinks called');
      this.cbsMenuSubitems.forEach((element) => {
        console.log(element.link, element.linkDesc, element.target);
        fetch(
          `https://vue-http-demo-3ffcc-default-rtdb.firebaseio.com/${this.dbName}.json`,
          {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },

            body: JSON.stringify({
              link: element.link,
              linkDesc: element.linkDesc,
              target: element.target,
            }),
          }
        )
          .then((response) => {
            if (response.ok) {
              return response.json();
            }
          })
          .then((data) => {
            this.isLoading = false;
            const results = [];
            for (const key in data) {
