<template>
  <button :disabled="editMode" v-if="adminMode" @click="editItem(linkIndex)">
    Edit
  </button>
  <button v-if="adminMode" @click="deleteItem(linkIndex)">Delete</button>
  <a :id="linkIndex" :href="link" :target="linkTarget">{{ linkDesc }}</a>
  <form
    v-if="editMode"
    @submit.prevent="
      saveItem(linkIndex, updatedLink, updatedLinkDesc, updatedLinkTarget)
    "
  >
    <div>
      <label>Link:</label><input type="text" v-model="updatedLink" /><br />
      <label>Link Description:</label
      ><input id="linkDescInput" type="text" v-model="updatedLinkDesc" /><br />
      <label>Target:</label
      ><input type="text" v-model="updatedLinkTarget" /><br />
      <label></label><button type="submit">Save</button>
      <button @click="editMode = false">Cancel</button>
    </div>
  </form>
</template>

<script>
export default {
  props: {
    adminMode: {
      type: Boolean,
      required: false,
      default: false,
    },
    linkIndex: {
      type: Number,
      required: true,
    },
    link: {
      type: String,
      required: true,
      default: '',
    },
    linkDesc: {
      type: String,
      required: true,
      default: '',
    },
    linkTarget: {
      type: String,
      required: false,
      default: '_blank',
    },
  },
  data() {
    return {
      editMode: false,
      updatedLink: this.link,
      updatedLinkDesc: this.linkDesc,
      updatedLinkTarget: this.linkTarget,
    };
  },
  emits: ['save-item', 'delete-item'],
  methods: {
    editItem(index) {
      console.log('editItem called for', index);
      this.editMode = true;
    },
    saveItem(linkIndex, link, linkDesc, linkTarget) {
      console.log('saveItem called for', linkIndex, link, linkDesc, linkTarget);
      this.$emit('save-item', linkIndex, link, linkDesc, linkTarget);
      this.editMode = false;
    },
    deleteItem(index) {
      if (confirm('Do you really want to delete?')) {
        this.$emit('delete-item', index);
      }
    },
  },
};
</script>

<style>
button {
  margin-right: 10px;
}
div {
  margin-left: 25px;
}
input {
  width: 300px;
}
label {
  display: inline-block;
  width: 150px;
  text-align: right;
  margin-right: 10px;
  margin-bottom: 5px;
}
</style>
