<template>
  <header>
    <h2>Printing</h2>
  </header>
  <ul>
    <li v-for="item in printMenuSubitems" :key="item.link">
      <a :href="item.link" :target="item.target">{{ item.linkDesc }}</a>
    </li>
  </ul>
  <button @click="PostLinks">Post Links</button>
  <button @click="PutLinks">Put Links</button>
  <button @click="GetLinks">Get Links</button>
</template>

<script>
export default {
  data() {
    return {
      dbName: 'my_print_links',
      printMenuSubitems: [
        {
          link: 'SuperCapeBinder.pdf',
          linkDesc: 'SuperCapeBinder.pdf',
          target: '_blank',
        },
        {
          link: 'importantprintinginstructions.pdf',
          linkDesc: 'instructions.pdf',
          target: '_blank',
        },
        {
          link: 'Safeguard Cancelation Form-1.pdf',
          linkDesc: 'Safeguard Cancelation Form-1.pdf',
          target: '_blank',
        },
        {
          link: 'Assurant Cancellation Form.pdf',
          linkDesc: 'Assurant Cancellation Form.pdf',
          target: '_blank',
        },
        {
          link: 'Wisemen2.pdf',
          linkDesc: 'Wisemen2.pdf',
          target: '_blank',
        },
        {
          link: '2026AmbetterKen.pdf',
          linkDesc: '2026AmbetterKen.pdf',
          target: '_blank',
        },
        {
          link: '2026AmbetterJenny.pdf',
          linkDesc: '2026AmbetterJenny.pdf',
          target: '_blank',
        },
      ],
    };
  },
  methods: {
    PostLinks() {
      this.printMenuSubitems.forEach((element) => {
        console.log(element.link, element.linkDesc, element.target);
        fetch(
          `https://vue-http-demo-3ffcc-default-rtdb.firebaseio.com/${this.dbName}.json`,
          {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },

            body: JSON.stringify({
              link: element.link,
              linkDesc: element.linkDesc,
              target: element.target,
            }),
          }
        )
          .then((response) => {
            if (response.ok) {
              return response.json();
            }
          })
          .then((data) => {
            this.isLoading = false;
            const results = [];
            for (const key in data) {
              results.push({
                id: key,
                link: data[key].link,
                linkDesc: data[key].linkDesc,
                target: data[key].target,
              });
            }
            this.results = results;
            console.log(this.results.length);
            console.log(this.results);
          })
          .catch((error) => {
            this.isLoading = false;
            this.error = 'Failed to load experiences';
            console.error('Error loading experiences:', error);
          });
      });
    },
    PutLinks() {
      console.log(
        this.printMenuSubitems.filter(
          (item) => item.link === 'SuperCapeBinder.pdf'
        )
      );
      fetch(
        `https://vue-http-demo-3ffcc-default-rtdb.firebaseio.com/${this.dbName}.json`,
        {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(this.printMenuSubitems),
        }
      )
        .then((response) => {
          if (response.ok) {
            return response.json();
          }
        })
        .then((data) => {
          this.isLoading = false;
          const results = [];
          for (const key in data) {
            results.push({
              id: key,
              link: data[key].link,
              linkDesc: data[key].linkDesc,
              target: data[key].target,
            });
          }
          this.results = results;
          console.log(this.results.length);
          console.log(this.results);
        })
        .catch((error) => {
          this.isLoading = false;
          this.error = 'Failed to load experiences';
          console.error('Error loading experiences:', error);
        });
    },
    GetLinks() {
      fetch(
        `https://vue-http-demo-3ffcc-default-rtdb.firebaseio.com/${this.dbName}.json`,
        {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
          },
        }
      )
        .then((response) => {
          if (response.ok) {
            return response.json();
          }
        })
        .then((data) => {
          this.isLoading = false;
          const results = [];
          for (const key in data) {
            results.push({
              id: key,
              link: data[key].link,
              linkDesc: data[key].linkDesc,
              target: data[key].target,
            });
          }
          this.results = results;
          console.log(this.results.length);
          console.log(this.results);
        })
        .catch((error) => {
          this.isLoading = false;
          this.error = 'Failed to load experiences';
          console.error('Error loading experiences:', error);
        });
    },
  },
};
</script>

<style scoped>
header {
  margin-left: 30px;
}
ul {
  list-style: none;
}
li {
  margin: 10px;
}
section {
  font-size: 24px;
}
</style>
